import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { format } from 'date-fns';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
  Spinner,
 
  

} from "reactstrap"
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
// import $ from "jquery";
import { French } from "flatpickr/dist/l10n/fr.js";
import Flatpickr from "react-flatpickr"
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
import url from "../../common/api"

class DetailEvenement extends Component {

  constructor(props) {
    super(props)

    this.state = { ok:"",evenement:{},systeme:[],unite:[],langue:[],thematique:[],
    mot:[],video:"",image:[],pjointe:[],localite:[],
    type_evenements:[],event:"",checkSysteme:[],irrigues:[],systeme_data:[],checkUnite:[],
    unites:[],unite_data:[],thematiques:[],thematique_data:[],checkThematique:[],chargement:false,hide:"none"
     }
     this.handleCheckSysteme.bind(this)
     this.handleCheckUnite.bind(this)
     this.handleCheckThematique.bind(this)
  }

  handleChargeAll=()=>{

    var id=localStorage.getItem("idEvenement")
    fetch(url+"evenements/"+id)
    .then((response) => response.json())
    .then((data) => {
        
      this.setState({systeme:data.type_systeme_irrigues})
      this.setState({unite:data.unite_administratives})
      this.setState({thematique:data.thematiques})
    })

  }

  handleDroit=()=>{
   $(".owener-close").css("display","none")
   //Type evenement
   $("#show-type-evenement").css("display","none")
   $("#valideRessource").css("display","none")
   $("#show-ok-te").css("display","none")

   //Tableaux
   $("#typesystemeirrigation").css("display","none")
   $("#thematique").css("display","none")
   $("#uniteadministrative").css("display","none")

   $("#show-tsi").css("display","none")
   $("#show-thematique").css("display","none")
   $("#show-ua").css("display","none")
  }

  componentDidMount(){
      //   const requestOptions = {
  //     method: 'GET',
  //     headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
  //     body: JSON.stringify(data)
  // };

    //Date

    this.handleCharge()
 

    var id=localStorage.getItem("idEvenement")
    
    this.handleCharge()
    this.handleChargeSysteme()
    this.handleChargeThematique()
    this.handleChargeUnite()

    fetch(url+"evenements/"+id)
    //fetch("http://localhost:3000/evenements/"+id)
    .then((response) => response.json())
    .then((data) => {
        this.setState({chargement:true})
        this.setState({evenement:data})
        console.log("la ressource:",data)
        this.setState({systeme:data.type_systeme_irrigues})
        this.setState({unite:data.unite_administratives})
        this.setState({thematique:data.thematiques})
      
        
        this.handleDroit()

        
        $("#libelle").val(data.libelle)
        $("#url").val(data.url)
        $("#description").val(data.description)
        $("#lieu").val(data.lieu)
        $("#objectif").val(data.objectif)
        $("#cible").val(data.cible)
        $("#organisateur").val(data.organisateur)
        $("#date_debut").val(data.date_debut)
        $("#date_fin").val(data.date_fin)
       // this.setState({chargement:true})
     
        try{
            $("#type_evenement").val(data.type_evenement.libelle)
            this.setState({event:data.type_evenement.libelle})
           }catch(e){
   
           }
        
       
      })
  }

  handleChargeAll=()=>{
    //this.setState({chargement:false})
    var id=localStorage.getItem("idEvenement")
    fetch(url+"evenements/"+id)
    .then((response) => response.json())
    .then((data) => {
      console.log("data",data)
      // if(JSON.parse(localStorage.getItem("authAshia")).id!=data.utilisateur.id){
      //   $(".owener").hide()
      // }else{
      //   $(".owener").show()
      // }
       
        this.setState({systeme:data.type_systeme_irrigues})
        this.setState({langue:data.langues})
        this.setState({unite:data.unite_administratives})
        this.setState({thematique:data.thematiques})
        this.setState({mot:data.mot_cles})
        this.setState({chargement:true})
        this.setState({localite:data.pays})
    })

  }

  handleRetour=()=>{
    this.setState({ok: <Redirect to='/liste-calendar'  />});
  }

  handleLibelle=()=>{
    document.getElementById("libelle").readOnly = false;
  }
  handleOrganisateur=()=>{
    document.getElementById("organisateur").readOnly = false;
  }
  handleDescription=()=>{
    document.getElementById("description").readOnly = false;
  }
  handleObjectif=()=>{
    document.getElementById("objectif").readOnly = false;
  }
  handleCible=()=>{
    document.getElementById("cible").readOnly = false;
  }
  handleLieu=()=>{
    document.getElementById("lieu").readOnly = false;
  }
  handleUrl=()=>{
    document.getElementById("url").readOnly = false;
  }

  handleTypeEvenement=()=>{
    
   
    $("#type_evenement").hide()
    $("#valideRessource").show()
    
    $("#no-show-ok-te").hide()
    $("#show-ok-te").show()
    $("#show-type-evenement").show()
 
  }
  
  handleTypeSystemeIrriguation=()=>{
    
    $("#typesystemeirrigation").show()
    $("#no-show-tsi").hide()
    $("#show-tsi").show()
  }

  handleUniteAdministrative=()=>{
    $("#uniteadministrative").show()
    $("#no-show-ua").hide()
    $("#show-ua").show()
  }

  handleThematique=()=>{
    $("#thematique").show()
    $("#no-show-thematique").hide()
    $("#show-thematique").show()
  }

  handleDDD=()=>{
    document.getElementById("date_debut").readOnly = false;
  }

  handleDDF=()=>{
    document.getElementById("date_fin").readOnly = false;
  }


  handleUpdateFormat=()=>{
    if($("#format option:selected").val()!=0){
    this.patch({"le_format_id":$("#show-format option:selected").val(),})
    $("#format").val($("#show-format option:selected").text())
    console.log("ok ok ok")
    }
    console.log("none")
    $("#format").show()
    $("#show-format").hide()
    $("#valideState").hide()

    $("#no-show-ok-format").show()
    $("#show-ok-format").hide()
}

handleCharge=()=>{


  fetch(url+"type_evenements")
  //fetch("http://localhost:3000/type_ressources")

  .then((response) => response.json())
  .then((data) => {
    console.log("=======>",data)
    
    this.setState({type_evenements:data})
   }
   
  );



}
handleChargeSysteme=()=>{
  fetch(url+"type_systeme_irrigues")
 

  .then((response) => response.json())
  .then((data) => {
    this.setState({systeme_data:data})
    data.map((element,idx) =>{
      this.setState({irrigues:[...this.state.irrigues,{value:element.id,label:element.libelle}]})
    })
    
    
   }
   
  );
     
}
handleChargeUnite=()=>{
   
  fetch(url+"unite_administratives")

  //fetch("http://localhost:3000/unite_administratives")

   .then((response) => response.json())
   .then((data) => {
      this.setState({unite_data:data})
     data.map((element,idx) =>{
       this.setState({unites:[...this.state.unites,{value:element.id,label:element.libelle}]})
     })
     
     
    }
    
   );
}
handleChargeThematique=()=>{
      
  fetch(url+"thematiques")
 

  .then((response) => response.json())
  .then((data) => {
    this.setState({thematique_data:data})
    data.map((element,idx) =>{
      this.setState({thematiques:[...this.state.thematiques,{value:element.id,label:element.libelle}]})
    })
   
   
   }
   
  );
    
}
patch=(data)=>{
  var id=localStorage.getItem("idEvenement")
  const requestOptions = {
      method: 'PATCH',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
      body: JSON.stringify(data)
  };

    fetch(url+"evenements/"+id, requestOptions)
    .then((response) => response.json())
    .then(response =>{console.log("ok,",response)
    this.handleChargeAll()
   
   } ,
   (error) => {
     
   })
}


handleUpdateDateDebut= ()=> {
    this.patch({"date_debut":$("#date_debut").val()})
    document.getElementById("date_debut").readOnly = true;
  }
handleUpdateDateFin= ()=> {
    this.patch({"date_fin":$("#date_fin").val()})
    document.getElementById("date_fin").readOnly = true;
}

handleUpdateLibelle=()=>{
 
  document.getElementById("libelle").readOnly = true;
  this.patch({"libelle":$("#libelle").val()})
}
handleUpdateOrganisateur=()=>{
  
  document.getElementById("organisateur").readOnly = true;
  this.patch({"organisateur":$("#organisateur").val()})
}
handleUpdateDescription=()=>{
  document.getElementById("description").readOnly = true;
  this.patch({"description":$("#description").val()})
}
handleUpdateObjectif=()=>{
  document.getElementById("objectif").readOnly = true;
  this.patch({"objectif":$("#objectif").val()})
}
handleUpdateCible=()=>{
  document.getElementById("cible").readOnly = true;
  this.patch({"cible":$("#cible").val()})
}
handleUpdateLieu=()=>{
  document.getElementById("lieu").readOnly = true;
  this.patch({"lieu":$("#lieu").val()})
}
handleUpdateUrl=()=>{
  document.getElementById("url").readOnly = true;
  this.patch({"url":$("#url").val()})
}
handleUpdateType=()=>{
  if($("#show-type-evenement option:selected").val()!=0){
  this.patch({"type_evenement_id":$("#show-type-evenement option:selected").val(),})
  $("#type_evenement").val($("#show-type-evenement option:selected").text())

  }

  $("#type_evenement").show()
  $("#show-type-evenement").hide()
  $("#valideRessource").hide()

  $("#no-show-ok-te").show()
  $("#show-ok-te").hide()
}
handleCheckSysteme=(data)=>{
  var  tab=[]
  this.setState({checkSysteme:tab})
  try{
      data.map((val,idx)=>{
          this.setState({checkSysteme:[...this.state.checkSysteme,{type_systeme_irrigue_id:val.value}]})
      })

  }catch(error){
      
  }

  }

  handleCheckUnite=(data)=>{
    var  tab=[]
    this.setState({checkUnite:tab})
    try{

        data.map((val,idx)=>{
            this.setState({checkUnite:[...this.state.checkUnite,{unite_administrative_id:val.value}]})
        })

    }catch(error){
        
    }
    
    }

    handleCheckThematique=(data)=>{
      var  tab=[]
      this.setState({checkThematique:tab})
  
      try{
         
          data.map((val,idx)=>{
              this.setState({checkThematique:[...this.state.checkThematique,{thematique_id:val.value}]})
          })
  
      }catch(error){
          
      }
     
      }

handleUpdateSysteme=()=>{
     
  this.patch({"evenement_systeme_irrigues_attributes":this.state.checkSysteme})
  $("#typesystemeirrigation").hide()
  $("#no-show-tsi").show()
  $("#show-tsi").hide()
}
  handleUpdateUnite=()=>{
      
  this.patch({"evenement_unite_administratives_attributes":this.state.checkUnite})
    
  
  $("#uniteadministrative").hide()
  $("#no-show-ua").show()
  $("#show-ua").hide()
  }
  handleUpdateThematique=()=>{
    this.patch({"evenement_thematiques_attributes":this.state.checkThematique})

        $("#thematique").hide()
        $("#no-show-thematique").show()
        $("#show-thematique").hide()
  } 

  handleCharge=()=>{}

  handleModifierEvenement=()=>{
    // $(".owener-close").hide()
    // $(".owener").show()
    // this.setState({hide2:"none"})
    // this.setState({hide:"block"})
    // this.handleCharge()
  }  
 
 render() {

  const animatedComponents = makeAnimated();

  const dataTest = [
    {
      value: 1, 
      label:"Test 1"
    },
    {
      value:2, 
      label:"Test 2"
    },
    {
      value:3, 
      label:"Test 3"
    }
  ];

  return (
          <React.Fragment>
          {
            (this.state.chargement)?
                (<>
              
            <div className="page-content">
            
               {this.state.ok}

            <MetaTags>
                <title>Détails de l'évenement</title>
            </MetaTags>
            <Container fluid={true}>
                <Row>
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                      <h4 className="mb-0 font-size-18">Détails de l'évenement</h4>
                      </div>
                  </Col>
                </Row>
     
                <hr/>

                <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                  <CardBody>

                  <Row>
                    <Col md="6">
                      <Button onClick={this.handleRetour} style={{backgroundColor:"#6A7172",float:"left",borderRadius:"10px"}} >
                        Retour
                      </Button>
                    </Col>
                    <Col md="6" id="droit">
                      <Button onClick={this.handleModifierEvenement} style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  color="success">
                        Modifier l'évenement
                      </Button>
                    </Col>
                  </Row>
                  <hr/>
                  <Row className="mt-4"> 
                    <Col md="8">
                    <label className="owener-close">Libellé</label>
                      <label className="owener">Libellé <a onClick={this.handleLibelle} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label>
                      <Input
                            className="form-control"
                            type="text"
                            id="libelle"
                            onBlur={this.handleUpdateLibelle}
                            readOnly
                        />
                    </Col>

                    <Col md="4">
                     <label className="owener-close">Organisateur</label>
                      <label className="owener">Organisateur <a onClick={this.handleOrganisateur} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label> 
                      <Input
                        className="form-control"
                        type="text"
                        id="organisateur"
                        onBlur={this.handleUpdateOrganisateur}
                        readOnly
                      />
                    </Col>
                  </Row>
                  
                  <Row className="mt-4"> 
                    <Col md="12" > 
                     <label className="owener-close">Description </label>
                      <label className="owener" >Description <a onClick={this.handleDescription} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label> 
                      <textarea  className="form-control" onBlur={this.handleUpdateDescription}  id="description" readOnly/>
                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                      

                    <Col md="4">
                    <label className="owener-close">Objectif</label>
                      <label className="owener">Objectif <a onClick={this.handleObjectif} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label>
                      <textarea
                        className="form-control"
          
                        id="objectif"
                        onBlur={this.handleUpdateObjectif}
                        readOnly
                      />
                    </Col>

                    <Col md="4">
                    <label className="owener-close">Cible</label>
                      <label className="owener">Cible <a onClick={this.handleCible} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label>
                      <Input
                            className="form-control"
                            type="text"
                            id="cible"
                            onBlur={this.handleUpdateCible}
                            readOnly
                        /> 
                    </Col>

                    <Col md="4">
                    <label className="owener-close">Lieu</label>
                      <label className="owener">Lieu <a onClick={this.handleLieu} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label>
                      <Input
                            className="form-control"
                            type="text"
                            id="lieu"
                            onBlur={this.handleUpdateLieu}
                            readOnly
                        /> 
                    </Col>

                    
                  </Row>

                  

                  

                  <Row className="mt-4"> 
                      <Col md="4">
                        <label className="owener-close">Types D'évenement</label>
                        <label className="owener" id="no-show-ok-te">Types D'évenement <a onClick={this.handleTypeEvenement} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label>
                        <label id="show-ok-te">Type D'évenement <a onClick={this.handleUpdateType} ><i class='bx bx-border bx-xs'>Modifier</i></a></label> 
                        <Input
                            className="form-control"
                            type="text"
                            id="type_evenement"
                            readOnly
                        />

                        <select className="form-select" aria-label="Default select example" id="show-type-evenement">
                        <option value="0" selected>{this.state.event}</option>
                        {
                            this.state.type_evenements.filter(e=>e.libelle!==this.state.event).map(
                                (element,idx) =>(
                                    <option value={element.id}>{element.libelle}</option>
                                )
                            )
                        }
                        </select>
                       
                      </Col>

                      <Col md="4">
                        <div id="ddd" >
                        <label className="owener-close">Date de débutt</label>
                          <label className="owener">Date de début <a onClick={this.handleDDD} ><i class='bx bx-border bxs-pencil bx-xs'></i></a> </label>
                          <Input
                              className="form-control"
                              type="text"
                              id="date_debut"
                              onBlur={this.handleUpdateDateDebut}
                              readOnly
                          />
                        </div>

                     
                       
                      </Col>


                    <Col md="4">
                        <div id="ddf" >
                          <label className="owener-close">Date de fin</label>
                          <label className="owener">Date de fin <a onClick={this.handleDDF} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label>
                          <Input
                              className="form-control"
                              type="text"
                              id="date_fin"
                              onBlur={this.handleUpdateDateFin}
                              readOnly
                          />
                    
                      </div>
                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                    <Col md="12"> 
                    <label className="owener-close">Url</label>
                      <label className="owener">Url <a onClick={this.handleUrl} ><i class='bx bx-border bxs-pencil bx-xs'></i></a></label>
                        <Input
                            className="form-control"
                            type="text"
                            id="url"
                            onBlur={this.handleUpdateUrl}
                            readOnly
                        /> 
                    </Col>
                  </Row>
                  </CardBody>
                </Card>


                <Row className="mb-4">
                    <Col md="6">
                        <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                        <CardBody>
                          <CardTitle className="mb-4 h4 owener-close">Type(s) de système(s) irrigué(s)</CardTitle>
                          <CardTitle id="no-show-tsi" className="mb-4 h4 owener">Type(s) de système(s) irrigué(s) <a onClick={this.handleTypeSystemeIrriguation} ><i class='bx bx-border bxs-plus-circle bx-xs'></i></a></CardTitle>
                          <CardTitle id="show-tsi" className="mb-4 h4">Type(s) de système(s) irrigué(s) <a onClick={this.handleUpdateSysteme}><i class='bx bx-border bx-xs'>Modifier</i></a></CardTitle>
                          <div id="typesystemeirrigation" className=" mt-4">
                              <ReactSelect
                                  closeMenuOnSelect={false}
                                  components={animatedComponents}
                                  isMulti
                                  onChange={this.handleCheckSysteme}
                                  options={this.state.irrigues}
                                  placeholder="Choisir la/les type(s) de système(s) irrigué(s)"
                              />
                          </div>
                          <div className=" mt-4">

                            <table className="table align-middle">
                                <thead>
                                    <tr>
                                        <th>Libelle</th> <th>Sigle</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        (this.state.systeme.length!=0)?

                                        (this.state.systeme.map(
                                            (element,idx)=>(
                                                <tr>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.libelle}</p>
                                                </td>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.sigle}</p>
                                                </td>
                                            </tr>
                                            )

                                        )):(

                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                        </tr>
                                        )

                                    }                              


                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>
                    </Col>
                    
                    <Col md="6">
                        <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                        <CardBody>
                          <CardTitle className="mb-4 h4 owener-close">Unité(s) administrative(s)</CardTitle>
                          <CardTitle id="no-show-ua" className="mb-4 h4 owener">Unité(s) administrative(s) <a onClick={this.handleUniteAdministrative} ><i class='bx bx-border bxs-plus-circle bx-xs'></i></a></CardTitle>
                          <CardTitle id="show-ua" className="mb-4 h4">Unité(s) administrative(s) <a onClick={this.handleUpdateUnite}><i class='bx bx-border bx-xs'>Modifier</i></a></CardTitle>
                          <div id="uniteadministrative" className=" mt-4">
                              <ReactSelect
                                  closeMenuOnSelect={false}
                                  components={animatedComponents}
                                  isMulti
                                  onChange={this.handleCheckUnite}
                                  options={this.state.unites}
                                  placeholder="Choisir la/les unité(s) administrative(s)"
                              />
                          </div>

                          <div className=" mt-4">
                            <table className="table align-middle">

                            <thead>

                                    <tr>
                                        <th>Libelle</th> <th>Code</th> 
                                    </tr>
                                </thead>
                                <tbody>

                                    
                                {
                                (this.state.unite !=0) ?
                                (this.state.unite.map(
                                    (element,idx)=>(
                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.libelle}</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.code}</p>
                                        </td>
                                    </tr>
                                    ))):(

                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                    </tr>
                                    )

                                

                                } 


                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>

                <Row>
                <Col md="6">
                        <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                        <CardBody>
                         <CardTitle className="mb-4 h4 owener-close">Thématique(s)</CardTitle>
                          <CardTitle id="no-show-thematique" className="mb-4 h4 owener">Thématique(s) <a onClick={this.handleThematique} ><i class='bx bx-border bxs-plus-circle bx-xs'></i></a></CardTitle>
                          <CardTitle id="show-thematique" className="mb-4 h4">Thématique(s) <a onClick={this.handleUpdateThematique}><i class='bx bx-border bx-xs'>Modifier</i></a></CardTitle>
                          <div id="thematique" className=" mt-4">
                              <ReactSelect
                                  closeMenuOnSelect={false}
                                  components={animatedComponents}
                                  isMulti
                                  onChange={this.handleCheckThematique}
                                  options={this.state.thematiques}
                                  placeholder="Choisir la/les thématique(s)"
                              />
                          </div>

                          <div className=" mt-4">
                            <table className="table align-middle">

                            {/* <thead>
                                    <tr>
                                        <th>Libelle</th>
                                    </tr>
                                </thead> */}
                                <tbody>
                                    
                                    {
                                    (this.state.thematique.length!=0)?
                                    (this.state.thematique.map(
                                        (element,idx)=>(
                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">{element.libelle}</p>
                                            </td>
                                            
                                        </tr>
                                        )
                                       )
                                    ):(  <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">La liste est vide</p>
                                        </td>
                                        
                                    </tr>)

                                    } 


                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                    

                    {/* <Col md="6">

                        <Card>
                        <CardBody>
                            <CardTitle className="mb-4 h4">Localisation(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">
                                <thead>
                                    <tr>
                                        <th>Code de la localité</th> <th>Nom de la localité</th>
                                    </tr>
                                </thead>
                                <tbody>
                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>

                    </Col> */}
                </Row>

                <hr></hr>
                

                
              </Container>
              </div>
                  </>):(
                    <div style={{marginTop:"250px",textAlign:"center"}}>
                       <Spinner type="grow" color="secondary" />
                </div>
                )
                
              }
          </React.Fragment>
        )}
  }
  export default DetailEvenement